import { Box, Button, FormControl, Input, Modal, ModalBody, ModalCloseButton, ModalContent, ModalFooter, ModalHeader, ModalOverlay, useDisclosure, useToast } from '@chakra-ui/react'
import axios from 'axios';
import React, { useState } from 'react'
import { ChatState } from '../../Context/ChatProvider';
import ChatLoading from '../ChatLoading';
import UserBadgeItem from '../userAvatar/UserBadgeItem';
import UserListItem from '../userAvatar/UserListItem';


const GroupChatModal = ({ children }) => {
    const { isOpen, onOpen, onClose } = useDisclosure();
    const [groupChatName, setGroupChatName] = useState();
    const [selectedUsers, setSelectedUsers] = useState([]);
    const [search, setSearch] = useState("");
    const [searchResult, setSearchResult] = useState([]);
    const [loading, setLoading] = useState(false);

    const toast = useToast();

    const { user, chats, setChats } = ChatState();

    const handleGroup = (userToAdd) => {
        console.log(userToAdd);
    if (selectedUsers.includes(userToAdd)) {
      toast({
        title: "User already added",
        status: "warning",
        duration: 5000,
        isClosable: true,
        position: "top",
      });
      return;
    }

        setSelectedUsers([...selectedUsers, userToAdd]);
    };

    const handleSearch = async (query) => {
        setSearch(query)
        if (!query) {
            return;
        }
        try { 
            setLoading(true)
            const config = {
                headers: {
                    Authorization: `Bearer ${user.token}`,
                }
            }
            const { data } = await axios.get(`/api/user?search=${search}`, config);
            setLoading(false);
            setSearchResult(data);
        } catch (err) {
            toast({
                title: "Error Occured",
                description: "Failed to load the search results",
                status: "error",
                duration: 5000,
                isClosable: true,
                position: "bottom-left"
            })
        }
    }


    const handleSubmit = async () => {
        if (!groupChatName || !selectedUsers) {
            toast({
                title: "Please fill all the fields",
                status: "warning",
                duration: 5000,
                isClosable: true,
                position: "top",
            });
            return;
        }

        try {
            const config = {
                headers: {
                    Authorization: `Bearer ${user.token}`,
                }
            }

            const { data } = await axios.post('/api/chat/group', {
                name: groupChatName,
                users: JSON.stringify(selectedUsers.map((u) => u._id)),
            }, config);

            setChats([data, ...chats])
            onClose();
            toast({
                title: "New Group Chat Created",
                status: "warning",
                duration: 5000,
                isClosable: true,
                position: "top",
            });
        } catch (error) {
            toast({
                title: "Failed to Create the Chat",
                description: error.response.data,
                status: "error",
                duration: 5000,
                isClosable: true,
                position: "top",
            });
        }
    }

    const handleDelete = (delUser) => {
        setSelectedUsers(selectedUsers.filter((sel) => sel._id !== delUser._id))
    }
    
    return (
    <span>
        <span onClick={onOpen}>{children}</span>  
          
          <Modal isOpen={isOpen} onClose={onClose}>
              <ModalOverlay />
              <ModalContent>
                  
                  <ModalHeader fontSize={"35px"} fontFamily={"Work sans"} display={"flex"} justifyContent={"center"}>
                        Create Group Chat
                  </ModalHeader>

                  <ModalCloseButton />

                  <ModalBody width={"100%"} display={"flex"} flexDir={"column"} alignItems={"center"}>
                      <FormControl>
                          <Input placeholder="Chat Name" mb={3} onChange={(e) => setGroupChatName(e.target.value)} />
                      </FormControl>
                      
                      <FormControl>
                          <Input placeholder="Add user eg: John, Bryan, Jane" mb={1} onChange={(e) => handleSearch(e.target.value)} />
                      </FormControl>
                      
                      <Box w={"100%"} display={"flex"} flexWrap={"wrap"}>
                          {selectedUsers.map((u) => (
                              <UserBadgeItem key={u._id} user={u} handleFunction={() => handleDelete(u)} />
                          ))}
                      </Box>
                      {loading ? (<ChatLoading />
                        ) : (
                            searchResult?.map((user) => (
                            <UserListItem key={user._id} user={user} handleFunction={() => handleGroup(user)} />
                            ))
                        )}
                  </ModalBody>
                  
                  <ModalFooter>
                      <Button colorScheme="blue" onClick={handleSubmit}>
                          Create Chat
                      </Button>
                  </ModalFooter>
              </ModalContent>
          </Modal>
    </span>
  )
}

export default GroupChatModal

